<template>
	<view class="container">
		<view class="coupon-top">
			<tui-tabs :tabs="tabs" :currentTab="currentTab" itemWidth="50%" @change="couponClassChange"></tui-tabs>
		</view>
		<view class="coupon-content-box">
			<view class="order-all" v-show="currentTab == 0 ? true : false">
				<NotUsed />
			</view>
			<view class="order-paid" v-show="currentTab == 1 ? true : false">
				<UseNote />
			</view>
			<view class="order-shipped" v-show="currentTab == 2 ? true : false">
				<CouponExpired />
			</view>
		</view>
	</view>
</template>

<script>
	import NotUsed from '@/pages/personal-pages/coupon/notUsed/notUsed.vue'
	import UseNote from '@/pages/personal-pages/coupon/useNote/useNote.vue'
	import CouponExpired from '@/pages/personal-pages/coupon/couponExpired/couponExpired.vue'
	export default {
		components: {
			NotUsed,
			UseNote,
			CouponExpired
		},
		data() {
			return {
				currentTab: 0,
				tabs: [{
					name: "未使用"
				}, {
					name: "使用记录"
				}, {
					name: "已过期"
				}]
			};
		},
		methods:{
			couponClassChange(e) {
				this.currentTab = e.index
			}
		}
	}
</script>

<style lang="scss" scoped>
	.container {
		width: 100%;
		height: 100vh;
		background-color: #f6f6f6;
		display: flex;
		flex-direction: column;

		.coupon-top {
			width: 100%;
		}
		
		.coupon-content-box{
			width: 100%;
			height: 100%;
			margin: 0 auto;
			margin-top: 20rpx;
			background-color: #fff;
		}
	}
</style>
